export default {
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			list: [1],
			page: 1,
			key: ''
		};
	},
	methods: {
		// 搜索
		search() {
			this.list = [];
			this.apiSearch();
		},
		// 搜索api
		apiSearch() {
			const vm = this;
			if (!vm.key) {
				vm.$utils.toast('请输入课程名称');
				return false;
			}
			COMMON.show();
			API('goodsList', {
					class_id: '',
					goods_name: self.data.key,
					page: self.data.page
				})
				.then((e) => {
					COMMON.hide();
					if (e.code == 200) {
						if (e.resData.goodsList.data.length == 0) {
							if (self.detail.length <= 0) {
								self.setData({
									detail: []
								})
								COMMON.Toast('未查询到该商品')
							} else {
								COMMON.Toast('已全部加载')
							}
						} else {
							self.setData({
								detail: [...self.detail, ...e.resData.goodsList.data]
							})
							console.log(self.detail)
						}
					}
				})
		}
	}
}
